.header {
  min-height: 74px;
  margin: 45px 0 0;
  border-bottom: 1px solid #545454B3;
  box-sizing: border-box;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
}

@media screen and (max-width: 642px) {
  .header {
    margin-top: 28px;
    min-height: 56px;
  }
}